import { getAll } from '../service';

const abstractDataReducer = (state = [], action = {}) => {
  switch (action.type) {
    case 'INIT_ABSTRACT_DATA': {
      return action.data;
    }
    default: {
      return state;
    }
  }
};

export const initialiseAbstractData = () => async (dispatch) => {
  const data = await getAll('abstract')();
  dispatch({
    type: 'INIT_ABSTRACT_DATA',
    data,
  });
};

export default abstractDataReducer;
